.trailer {
  color: white;
  border-radius: 4px;
  border-color:black;
  background-color: #3a3a3d;
  text-align: center;
  padding: 8px 12px;
  text-decoration: none;
  font-size: 13px;
  font-weight: bold;
  margin-top: 20px;
  margin-bottom: 20px;
}

.trailer:hover{
  background-color:#464649;
  cursor: pointer;
}

.trailer:focus{
  outline: none;
}
