.main-container {
  text-align: center;
  padding-top: 50px;
  color: white;
}

.game-header {
  height: 250px;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
}

.game-logo {
  float: left;
  width: 144px;
  height: 192px;
  margin-top: 30px;
  margin-left: 40px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.steam-button {
  color: white;
  border-radius: 4px;
  border-color: black;
  background-color: #3a3a3d;
  text-align: center;
  padding: 8px 12px;
  text-decoration: none;
  font-size: 13px;
  font-weight: bold;
  margin: 0;
}

.steam-button:hover {
  background-color: #464649;
  cursor: pointer;
}

.steam-button:focus {
  outline: none;
}

.streams {
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: center;
}

.stream-box {
  height: 200px;
  margin: 10px;
  transition: all 0.3s ease-out;
}

.stream-box:hover {
  box-shadow: -1px 1px 0px red, -2px 2px 0px red, -3px 3px 0px red,
    -4px 4px 0px red, -5px 5px 0px red;
  transform: translate(5px, -5px);
}

.stream-box-blue {
  height: 200px;
  margin: 10px;
  transition: all 0.3s ease-out;
}

.stream-box-blue:hover {
  box-shadow: -1px 1px 0px blue, -2px 2px 0px blue, -3px 3px 0px blue,
    -4px 4px 0px blue, -5px 5px 0px blue;
  transform: translate(5px, -5px);
}

a {
  color: white;
}

a:hover {
  color: white;
}

h1,
h2 {
  color: white;
}
